body {
	/*background-color: #52739e;*/
	background: rgb(55,15,49);
	background: -webkit-gradient(linear, left top, right top, from(rgba(55,15,49,1)), color-stop(63%, rgba(63,71,107,1)));
	background: -webkit-linear-gradient(left, rgba(55,15,49,1) 0%, rgba(63,71,107,1) 63%);
	background: -o-linear-gradient(left, rgba(55,15,49,1) 0%, rgba(63,71,107,1) 63%);
	background: linear-gradient(90deg, rgba(55,15,49,1) 0%, rgba(63,71,107,1) 63%);
	padding: 0;
	font-family: sans-serif;
}



